import React from "react";
import Image from "next/image";
import Link from "next/link";
import { projects } from "../../content/projects";

export default function Projeler() {
  return (
    <main style={{ maxWidth: 1200, margin: '0 auto', padding: '2.5rem 1rem' }}>
      <h1 style={{ fontSize: '2.2rem', marginBottom: 32 }}>Projeler</h1>
      <div style={{ display: 'flex', flexWrap: 'wrap', gap: 32, justifyContent: 'flex-start' }}>
        {projects.map((project) => (
          <Link
            key={project.slug}
            href={`/projeler/${project.slug}`}
            style={{
              flex: '1 1 400px',
              maxWidth: 420,
              minWidth: 320,
              background: 'none',
              borderRadius: 22,
              boxShadow: 'none',
              display: 'flex',
              flexDirection: 'column',
              textDecoration: 'none',
              color: '#111',
              transition: 'none',
              cursor: 'pointer',
              overflow: 'visible',
              height: 700,
              fontFamily: 'Segoe UI, Arial, sans-serif',
            }}
          >
            <div
              style={{
                background: '#fff',
                borderRadius: 22,
                boxShadow: '0 6px 32px #0001',
                display: 'flex',
                flexDirection: 'column',
                height: 700,
                transition: 'box-shadow 0.2s, transform 0.2s',
                overflow: 'hidden',
              }}
              onMouseEnter={e => {
                e.currentTarget.style.transform = 'scale(1.045)';
                e.currentTarget.style.boxShadow = '0 12px 36px #0070f344';
              }}
              onMouseLeave={e => {
                e.currentTarget.style.transform = 'scale(1)';
                e.currentTarget.style.boxShadow = '0 6px 32px #0001';
              }}
            >
              <div style={{ width: '100%', height: 260, background: '#f5f5f5', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Image
                  src={project.image}
                  alt={project.title}
                  width={400}
                  height={260}
                  style={{ objectFit: 'contain', width: '100%', height: '100%' }}
                />
              </div>
              <div style={{ flex: 1, display: 'flex', flexDirection: 'column', justifyContent: 'space-between', padding: '2rem 1.7rem 2.2rem 1.7rem', rowGap: 18 }}>
                <div>
                  <h2 style={{ fontSize: '1.38rem', marginBottom: 14, fontWeight: 600, letterSpacing: 0.2, lineHeight: 1.3 }}>{project.title}</h2>
                  <p style={{ color: '#444', fontSize: '1.05rem', marginBottom: 20, lineHeight: 1.7, letterSpacing: 0.01 }}>{project.description}</p>
                </div>
                <div style={{ color: '#888', fontSize: '1.08rem', marginTop: 'auto', letterSpacing: 0.01, lineHeight: 1.5 }}>{project.date}</div>
              </div>
            </div>
          </Link>
        ))}
      </div>
    </main>
  );
} 